import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import { Http, Headers, Response } from '@angular/http';
import { User } from './user';

@Injectable()
export class UserService {
  constructor(private http: Http) {}
  private oauthUrl = 'http://siatra.localhost/oauth/token';
  private usersUrl = 'http://siatra.localhost/api/users';

  getAccessToken() {
    const headers = new Headers({
      'Content-Type': 'application/json',
      Accept: 'application/json'
    });

    const postData = {
      grant_type: 'password',
      client_id: '4',
      client_secret: 'aZB8dCT1iBgrQfO3CEoccAazh4Ru4Pc8psfspMQl',
      username: 'atxaloisio@hotmail.com',
      password: 'mestre',
      scope: '*'
    };

    return this.http.post(this.oauthUrl, JSON.stringify(postData), {
        headers: headers
      })
      .map((res: Response) => res.json())
      .catch((error: any) =>
        Observable.throw(error.json().error || 'Server error')
      );
  }

  getUsuarioAPI(): Observable<User[]>  {
    let headers = new Headers();
    headers.append('Authorization', 'Basic ' + btoa('atxaloisio@hotmail.com:mestre'));
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://siatra.localhost/api/users', {headers: headers})
    .map((res: Response) => res.json())
    .catch((error: any) =>
      Observable.throw(error.json().error || 'Server error')
    );
  }

  getUsers(accessToken: string): Observable<User[]> {
    const headers = new Headers({
      Accept: 'application/json',
      Authorization: 'Bearer ' + accessToken
    });

    return this.http
      .get(this.usersUrl, {
        headers: headers
      })
      .map((res: Response) => res.json())
      .catch((error: any) =>
        Observable.throw(error.json().error || 'Server error')
      );
  }
}



