import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'app';
  getToken() {
    const token = document.querySelector('meta[name="csrf-token"]')[
      'content'
    ];
    console.log(token);
    return token;
  }
}



